import {
  Component,
  OnInit,
  ViewChild,
  ElementRef,
  AfterViewInit
} from '@angular/core';

@Component({
  selector: 'app-shoot',
  templateUrl: './shoot.component.html',
  styles: []
})
export class ShootComponent implements OnInit, AfterViewInit {
  public canvasIsVisible = false;
  public onScreenText = '';
  @ViewChild('video') public video: ElementRef;
  @ViewChild('canvas') public canvas: ElementRef;

  public captures: Array<any>;

  public constructor() {
    this.captures = [];
  }

  ngOnInit() {
    this.countDown();
  }

  countDown() {
    this.onScreenText = '3';
    setTimeout(() => (this.onScreenText = '2'), 1000);
    setTimeout(() => (this.onScreenText = '1'), 2000);
    setTimeout(() => this.capture(), 3000);
  }

  public ngAfterViewInit() {
    if (navigator.mediaDevices && navigator.mediaDevices.getUserMedia) {
      navigator.mediaDevices.getUserMedia({ video: true }).then(stream => {
        this.video.nativeElement.src = window.URL.createObjectURL(stream);
        this.video.nativeElement.play();
      });
    }
  }

  public capture() {
    this.onScreenText = '';
    this.canvas.nativeElement
      .getContext('2d')
      .drawImage(this.video.nativeElement, 0, 0, 1280, 720);
    this.captures.push(this.canvas.nativeElement.toDataURL('image/png'));
    this.canvasIsVisible = true;
    setTimeout(function() {
      this.canvasIsVisible = false;

      if (this.captures.length < 4) {
        this.countDown();
      } else {
        alert('processing!');
      }
    }, 1500);
  }
}
